import { FormContainer, InputWrapper, InputLabel } from "./form.styled";
import InputField from "./InputField";

const Address = () => {
  return (
    <FormContainer>
      <InputWrapper>
        <InputLabel></InputLabel>
        <InputField
          placeholder=""
          name="customerName"
          type="text"
          maxLength={20}
        />
      </InputWrapper>
      <InputWrapper>
        <InputLabel></InputLabel>
        <InputField
          placeholder=""
          name="phoneNumber"
          type="text"
          maxLength={10}
        />
      </InputWrapper>
      <InputWrapper>
        <InputLabel></InputLabel>
        <InputField
          placeholder=""
          name="email"
          type="text"
          maxLength={30}
        />
      </InputWrapper>
      <InputWrapper>
        <InputLabel></InputLabel>
        <InputField
          placeholder=""
          name="address"
          type="text"
          maxLength={30}
        />
      </InputWrapper>
    </FormContainer>
  );
};

export default Address;
